        doEffectsAnimation: function (group) {
            const doAnimation = (index) => {
                Utils.timeout(40).then(() => {
                	if (group.children.length === 0) {
                		return;
					}
					if (!group.visible) {
                		doAnimation(index);
                		return;
					}
                    if (0 === index) {
                        group.children[group.children.length-1].visible = false;
                    }
                    else {
                        group.children[index-1].visible = false;
                    }
                    group.children[index].visible = true;
                    doAnimation(++index % group.children.length);
                });
            };

            doAnimation(0);
        },

