Utils.timeStamp = -1;        // 
Utils.timeOffset = 0;       // 
Utils.sensitiveWords = [];  // 
Utils.selOptions = [];      // uuid

export class ResourcesManager {
    static forEachReCount(fun) {
        Object.keys(this.refCount.audio).forEach(key => fun && fun('audio', key, this.getCount('audio', key)));
        Object.keys(this.refCount.img).forEach(key => fun && fun('img', key, this.getCount('img', key)));
    }

    static resetReCount() {
        Object.keys(this.refCount.audio).forEach(key => {
            const { global=0 } = this.refCount.audio[key];
            this.refCount.audio[key] = { global };
        });
        Object.keys(this.refCount.img).forEach(key => {
            const { global=0 } = this.refCount.img[key];
            this.refCount.img[key] = { global };
        });
    }

    static removeFromCache(type, key) {
        if (!this.refCount[type][key]) {
            return;
        }
        if (this.getCount(type, key)) {
            return;
        }
        if (type === 'audio' && this.cache) {
            if ('ios' === flashvars.client && flashvars.app_version >= '1.5.0') {
                PhoneUtils.removeAudioUrl(key);
            }
            else {
                const sound = this.cache.getSound(key);
                if (sound && sound.audioTag && sound.data) {
                    sound.data.free = true;
                }
                if (sound && /blob:/i.test(sound.url)) {
                    URL.revokeObjectURL(sound.url);
                }
                this.cache.checkSoundKey(key) && this.cache.removeSound(key);
            }

            delete this.refCount[type][key];
        }

        if (type === 'img' && this.cache) {
            const image = this.cache.getImage(key, true);
            if(image){
                const { frames } = image.data;
                frames && frames.forEach((frame) => {
                    URL.revokeObjectURL(frame.blobUrl);
                    frame.blobUrl = null;
                });
                /blob:/i.test(image.url) && URL.revokeObjectURL(image.url);
                this.cache.removeImage(key);
            }
            delete this.refCount[type][key];
        }
    }

    static transferRefCount(type, fKey, fRefType, tKey, tRefType) {
        this.refCount[type][tKey] = this.refCount[type][tKey] || {};
        this.refCount[type][tKey][tRefType] = this.refCount[type][tKey][tRefType] || 0;
        if (!this.refCount[type][fKey]) {
            return;
        }
        this.refCount[type][tKey][tRefType] += this.refCount[type][fKey][fRefType];
        this.refCount[type][fKey][fRefType] = 0;
        this.removeFromCache(type, fKey);
    }

    static transferByRefCount(fRefType, tRefType) {
        Object.keys(this.refCount.audio).forEach(key => {
            const refCount = this.refCount.audio[key];
            refCount[tRefType] = (refCount[tRefType] || 0)+(refCount[fRefType] || 0);
            refCount[fRefType] = 0;
        });
        Object.keys(this.refCount.img).forEach(key => {
            const refCount = this.refCount.img[key];
            refCount[tRefType] = (refCount[tRefType] || 0)+(refCount[fRefType] || 0);
            refCount[fRefType] = 0;
        });
    }
    
    static addRefCount(type, key, refType='play', count=1) {
        if (!type || !key) {
            return;
        }
        this.refCount[type][key] = this.refCount[type][key] || {};
        this.refCount[type][key][refType] = (this.refCount[type][key][refType] || 0) + count;
    }

    static subRefCount(type, key, refType='play') {
        if (this.refCount[type][key]) {
            const refCount = this.refCount[type][key];
            refCount[refType] = (refCount[refType] || 0) - 1;
            refCount[refType] = refCount[refType] < 0 ? 0 : refCount[refType];
            this.removeFromCache(type, key);
        }
        else if ('audio' === type && 'ios' === flashvars.client && '1.5.0' <= flashvars.app_version) {
            !!key && PhoneUtils.removeAudioUrl(key);
        }
    }

    static delRefTypeCount(type, key, refType) {
        if (this.refCount[type][key]) {
            const refCount = this.refCount[type][key];
            refCount[refType] = 0;
            this.removeFromCache(type, key);
        }
    }

    static delByRefType(refType){
        Object.keys(this.refCount.audio).every(key => {
            this.refCount.audio[key][refType] = 0;
            this.removeFromCache('audio', key);
        });
        Object.keys(this.refCount.img).every(key => {
            this.refCount.img[key][refType] = 0;
            this.removeFromCache('img', key);
        });
    }

    static getCount(type, key, refType = '') {
        return this.refCount[type][key]
            ? (refType ? (this.refCount[type][key][refType] || 0) : Object.keys(this.refCount[type][key]).reduce((acc, cur) => acc + this.refCount[type][key][cur], 0))
            : 0;
    }

    static getResourcesByContext(context, chapter, pIndex = 0, cmdInfo, cIndex =0) {
        const lastUsedKeyMap = this.lastUsedKeyMap;
        const resources = { data: [], size: 0 };
        let frame = context && context.stack[0];
        let cmdFrame = context && context.cmdStack[0];

        if (!chapter || !chapter.plots || !chapter.plots[pIndex] || cmdInfo && !cmdInfo.commands[cIndex]) {
            return Promise.resolve(resources);
        }

        const isMaxSize = (isCommand = false) => resources.size > this.MaxSize || !isCommand && resources.data.length > this.MaxCount;

        const addResource = (url, type, preloadType) => {
            this.addRefCount(type, url, preloadType);
            if (!resources.data.some(d => d.key === url)) {
                return xhr.getResourceSize(url, type).then(size => {
                    resources.data.push({ key: url, type, size });
                    resources.size += size;
                });
            }
            return Promise.resolve();
        };

        const addWeatherResource = (type, preloadType) => {
            const promises = [];
            if (type === 'snowy') {
                for (let i=0; i<120; i++) {
                    promises.push(addResource('/weather/snow/xuehua_' + Utils.formatNum(i) + '.png', 'img', preloadType));
                }
            }
            else if (type === 'rainy') {
                for (let i=0; i<50; i++) {
                    promises.push(addResource('/weather/rain/xiayu_' + Utils.formatNum(i) + '.png', 'img', preloadType));
                }
            }
            else if (type === 'defoliation') {
                for (let i=0; i<90; i++) {
                    promises.push(addResource('/weather/mapleLeaves/fengye_' + Utils.formatNum(i) + '.png', 'img', preloadType));
                }
            }
            else if (type === 'fallingPeach') {
                for (let i=0; i<90; i++) {
                    promises.push(addResource('/weather/beachBlossom/taohua_' + Utils.formatNum(i) + '.png', 'img', preloadType));
                }
            }
            else if (type === 'fallingFlower') {
                for (let i=0; i<90; i++) {
                    promises.push(addResource('/weather/blossom/yinghua_' + Utils.formatNum(i) + '.png', 'img', preloadType));
                }
            }
            else if (type === 'lightning') {
                for (let i=0; i<50; i++) {
                    promises.push(addResource('/weather/lightning/shandian_' + Utils.formatNum(i) + '.png', 'img', preloadType));
                }
            }
            return Promise.resolve(promises);
        };

        const getInCmdInfo = (cmdInfo, cIndex, canDeepPreload, preloadType) => {
            if (cIndex >= cmdInfo.commands.length) {
                return Promise.resolve();
            }

            const getInCommand = (command, preloadType) => {
                if ('addImage' === command.type) {
                    const { index, url } = command.args[0];
                    if (index === 1) {
                        if ('inherit' === url) {
                            this.addRefCount('img', lastUsedKeyMap.background, preloadType);
                            return Promise.resolve();
                        } else {
                            lastUsedKeyMap.background = url;
                        }
                    }
                    return addResource(url, 'img', preloadType);
                } else if ('addImage' === command.type && 'inherit' !== command.args[0].url) {
                    return addResource(command.args[0].url, 'img', preloadType);
                } else if (('playMusic' === command.type || 'playSound' === command.type) && 'inherit' !== command.args[0].url) {
                    let url = command.args[0].url;
                    if ('playMusic' === command.type) {
                        if (this.forceDecode.musics.length < this.MaxForceDecodeMusic) {
                            this.forceDecode.musics.push(url);
                        }
                        lastUsedKeyMap.music = url;
                    } else {
                        lastUsedKeyMap.sounds[command.args[0].index] = url;
                        (!this.forceDecode.sounds[command.args[0].index]) && (this.forceDecode.sounds[command.args[0].index] = url);
                    }
                    return addResource(url, 'audio', preloadType, preloadType);
                } else if (('playMusic' === command.type || 'playSound' === command.type) && 'inherit' === command.args[0].url) {
                    if ('playMusic' === command.type) {
                        this.addRefCount('audio', lastUsedKeyMap.music, preloadType);
                    } else {
                        this.addRefCount('audio', lastUsedKeyMap.sounds[command.args[0].index], preloadType);
                    }
                    return Promise.resolve();
                } else if ('showDialog' === command.type) {
                    return addResource(command.args[0].avatar.url, 'img', preloadType);
                } else if (canDeepPreload && ('options' === command.type || 'conditionOptions' === command.type)) {
                    let promise = Promise.resolve();
                    for (let option of command.args) {
                        if (option.background) {
                            if (option.background.normal) {
                                promise = promise.then(() => addResource(option.background.normal.url, 'img', preloadType + 'Option'));
                            }
                            if (option.background.active) {
                                promise = promise.then(() => addResource(option.background.active.url, 'img', preloadType + 'Option'));
                            }
                            if (option.background.disable) {
                                promise = promise.then(() => addResource(option.background.disable.url, 'img', preloadType + 'Option'));
                            }
                        }
                        if (option.sound && option.sound.url) {
                            promise = promise.then(() => addResource(option.sound.url, 'audio', preloadType + 'Option'));
                        }
                    }
                    return promise;
                } else if ('weather' === command.type && '1' !== flashvars.offline) {
                    return addWeatherResource(command.args[0], preloadType);
                }
                return Promise.resolve();
            };

            const preloadOptions = options => {
                let promise = Promise.resolve();
                const { background, music, sounds } = this.lastUsedKeyMap;
                for (let opIndex = 0; options && opIndex < options.length; opIndex++) {
                    for (let opPlotIndex = 0; opPlotIndex < options[opIndex].commands.length && opPlotIndex < 10; opPlotIndex++) {
                        promise = promise.then(() => {
                            this.lastUsedKeyMap = { background, music, sounds: [...sounds] };
                            return getInCommand(options[opIndex].commands[opPlotIndex], 'PreloadOption' + opPlotIndex).then(() => {
                                if (isMaxSize(true)) {
                                    return Promise.resolve();
                                } else {
                                    const {type, args} = options[opIndex].commands[opPlotIndex];
                                    if (type === 'options' || type === 'conditionOptions') {
                                        if (canDeepPreload) {
                                            return preloadOptions(args);
                                        } else {
                                            return Promise.resolve();
                                        }
                                    }
                                }
                            });
                        }).then(() => {
                            this.lastUsedKeyMap = { background, music, sounds: [...sounds] };
                            return Promise.resolve();
                        });
                    }
                }
                return promise;
            };

            const getInNextCommand = (cmdInfo, cIndex, canDeepPreload, preloadType) => {
                if (cmdInfo.commands.length > cIndex) {
                    return getInCmdInfo(cmdInfo, cIndex, canDeepPreload, preloadType);
                } else if (cmdFrame) {
                    if (cmdFrame.type === 'insert') {
                        return Promise.resolve();
                    } else if (cmdFrame.loopCondition) {
                        if (!cmdInfo.parentInfo) {
                            return Promise.resolve();
                        }
                        cIndex = cmdInfo.parentCmdIndex + 1;
                        cmdInfo = cmdInfo.parentInfo;
                        cmdFrame = null;
                        return getInCmdInfo(cmdInfo, cIndex, false, preloadType);
                    } else {
                        cmdFrame = null;
                        return getInCmdInfo(cmdInfo, 0, false, preloadType);
                    }
                }
                return Promise.resolve();
            };

            return getInCommand(cmdInfo.commands[cIndex], preloadType).then(() => {
                if (isMaxSize(true)) {
                    return Promise.resolve();
                } else {
                    const { type, args }= cmdInfo.commands[cIndex];
                    if (type === 'back' || type === 'jump' || type === 'insert') {
                        return Promise.resolve();
                    } else if (type === 'options' || type === 'conditionOptions') {
                        if (canDeepPreload) {
                            return preloadOptions(args).then(() => getInNextCommand(cmdInfo, cIndex + 1, false, preloadType));
                        } else {
                            return Promise.resolve();
                        }
                    } else {
                        return getInNextCommand(cmdInfo, cIndex + 1, true, preloadType);
                    }
                }
                return Promise.resolve();
            });
        };

        const getPlotOptionCount = plot => {
            let count = 0;
            for (let opIndex = 0; plot.options && opIndex < plot.options.length; opIndex++) {
                count += plot.options[opIndex].plots.length;
            }

            for (let opIndex = 0; plot.condition_options && opIndex < plot.condition_options.length; opIndex++) {
                count += plot.condition_options[opIndex].plots.length;
            }
            return count;
        };

        const getInChapter = (chapter, pIndex,  canDeepPreload ,preloadType) => {
            const promises = [];
            const plot = chapter.plots[pIndex];

            if (!plot || plot.commands && !plot.commands.length) {
                return Promise.resolve();
            }

            if(plot.commands){
                return getInCmdInfo(plot, 0, canDeepPreload, preloadType);
            }

            // sound
            if (Object.prototype.toString.call(plot.plot_sound) == '[object Array]') {
                for (let i in plot.plot_sound) {
                    let item = plot.plot_sound[i];
                    if (item && 'inherit' !== item.url) {
                        promises.push(addResource(item.url, 'audio', preloadType));
                        lastUsedKeyMap.sounds[i] = item.url;
                        (!this.forceDecode.sounds[i]) && (this.forceDecode.sounds[i] = item.url);
                    } else if (item && item.url === 'inherit') {
                        this.addRefCount('audio', lastUsedKeyMap.sounds[i], preloadType);
                    }
                }
            } else if (plot.plot_sound.url && 'inherit' !== plot.plot_sound.url) {
                promises.push(addResource(plot.plot_sound.url, 'audio'));
            }
            // background
            if (plot.background.url) {
                if (plot.background.url !== 'inherit') {
                    promises.push(addResource(plot.background.url, 'img', preloadType));
                    lastUsedKeyMap.background = plot.background.url;
                } else {
                    this.addRefCount('img', lastUsedKeyMap.background, preloadType);
                }
            }
            // characters
            for (let character of plot.characters) {
                if (character.url) {
                    promises.push(addResource(character.url, 'img', preloadType));
                }
            }
            // avatar
            if (plot.avatar.url) {
                promises.push(addResource(plot.avatar.url, 'img', preloadType));
            }
            // music
            if (plot.music.url && plot.music.url !== 'inherit') {
                if (this.forceDecode.musics.length < this.MaxForceDecodeMusic) {
                    this.forceDecode.musics.push(plot.music.url);
                }
                promises.push(addResource(plot.music.url, 'audio', preloadType));
                lastUsedKeyMap.music = plot.music.url;
            } else if (plot.music.url === 'inherit') {
                this.addRefCount('audio', lastUsedKeyMap.music, preloadType);
            }
            // props
            for (let prop of plot.props) {
                if (prop.url) {
                    promises.push(addResource(prop.url, 'img', preloadType));
                }
            }
            // options
            for (let option of plot.options) {
                if (option.background) {
                    if (option.background.normal) {
                        promises.push(addResource(option.background.normal.url, 'img', preloadType+'Option'));
                    }
                    if (option.background.active) {
                        promises.push(addResource(option.background.active.url, 'img', preloadType+'Option'));
                    }
                    if (option.background.disable) {
                        promises.push(addResource(option.background.disable.url, 'img', preloadType+'Option'));
                    }
                }
                if (option.sound && option.sound.url) {
                    addResource(option.sound.url, 'audio', preloadType+'Option');
                }
            }
            // weather
            if (plot.weather.value && '1' !== flashvars.offline) {
                addWeatherResource(plot.weather.value, preloadType).then(rp => promises.push(...rp));
            }

            const preloadPlotOptions = () => {
                let promise = Promise.resolve();
                const { background, music, sounds } = this.lastUsedKeyMap;

                for (let opIndex = 0; plot.options && opIndex < plot.options.length; opIndex++) {
                    const optChapter = {plots: plot.options[opIndex].plots};
                    for (let opPlotIndex = 0; opPlotIndex < plot.options[opIndex].plots.length && opPlotIndex < 2; opPlotIndex++) {
                        promise = promise.then(() => {
                            this.lastUsedKeyMap = { background, music, sounds: [...sounds] };
                            return getInChapter(optChapter, opPlotIndex, false, 'PreloadOption' + opPlotIndex);
                        }).then(() => {
                            this.lastUsedKeyMap = { background, music, sounds: [...sounds] };
                            return Promise.resolve();
                        });
                    }
                }

                for (let opIndex = 0; plot.condition_options && opIndex < plot.condition_options.length; opIndex++) {
                    const optChapter = {plots: plot.condition_options[opIndex].plots};
                    for (let opPlotIndex = 0; opPlotIndex < plot.condition_options[opIndex].plots.length && opPlotIndex < 3; opPlotIndex++) {
                        promise = promise.then(() => {
                            this.lastUsedKeyMap = { background, music, sounds: [...sounds] };
                            return getInChapter(optChapter, opPlotIndex, false, 'PreloadConOption' + opPlotIndex);
                        }).then(() => {
                            this.lastUsedKeyMap = { background, music, sounds: [...sounds] };
                            return Promise.resolve();
                        });
                    }
                }

                return promise;
            };

            const getInNextPlot = (chapter, pIndex, canDeepPreload, preloadType) => {
                if (chapter.plots.length > pIndex) {
                    return getInChapter(chapter, pIndex, canDeepPreload, preloadType);
                } else if (frame) {
                    if (frame.type === 'insert') {
                        return Promise.resolve();
                    } else if (frame.loopCondition) {
                        if (!chapter.parent) {
                            return Promise.resolve();
                        }
                        pIndex = frame.isCommand ? chapter.parent.pnPlot : chapter.parent.pnPlot + 1;
                        chapter = chapter.parent.pChapter;
                        frame = null;
                        return getInChapter(chapter, pIndex, false, preloadType);
                    } else {
                        frame = null;
                        return getInChapter(chapter, 0, false, preloadType);
                    }
                }
                return Promise.resolve();
            };

            return Promise.all(promises).then(() => {
                if (isMaxSize() || !canDeepPreload) {
                    return Promise.resolve();
                } else {
                    if (getPlotOptionCount(plot)) {
                        return preloadPlotOptions().then(() => getInNextPlot(chapter, pIndex + 1, false, preloadType));
                    } else {
                        return getInNextPlot(chapter, pIndex + 1, true, preloadType);
                    }
                }
            });
        };

        return cmdInfo
            ? getInCmdInfo(cmdInfo, cIndex, true, 'play').then(() => resources)
            : getInChapter(chapter, pIndex, true, 'play').then(() => resources);
    }

    static getResourcesSize(resources) {
        const promises = [];
        resources.forEach(r => {
            promises.push(xhr.getResourceSize(r.key, r.type))
        });
        return Promise.all(promises).then(sizes => sizes.reduce((a,b) => a + b, 0));
    }
}

ResourcesManager.MaxSize = 200000000;
ResourcesManager.MaxCount = 64;
ResourcesManager.MaxForceDecodeMusic = 3;
ResourcesManager.refCount = { audio: {}, img: {} };
ResourcesManager.usingKeyMap = { background: '', sounds: new Array(10).fill(''), music: '' };
ResourcesManager.forceDecode = { musics: [], sounds: new Array(10).fill('') };
ResourcesManager.lastUsedKeyMap = { background: '', sounds: new Array(10).fill(''), music: '' };


export class AudioTagPool {
    static get() {
        let freeAudio = AudioTagPool.pool.find(a => a.free);
        if (freeAudio) {
            freeAudio.pause();
            freeAudio.free = false;
        } else {
            freeAudio = document.createElement('audio');
        }
        return freeAudio;
    }

    static exist(audio){
        return AudioTagPool.pool.some(a => a === audio);
    }

    static fill() {
        const addAudio = len => {
            for (let i = 0; i < len; i++) {
                let audio = document.createElement('audio');
                audio.free = true;
                audio.loop = false;
                audio.play();
                AudioTagPool.pool.push(audio);
            }
        };
        if (AudioTagPool.pool.length) {
            const freeCount = AudioTagPool.pool.reduce((acc, cur) => acc + (cur && cur.free ? 1 : 0), 0);
            if (freeCount < 10) {
                addAudio(50);
            }
        } else {
            addAudio(100);
        }
    }
}
AudioTagPool.pool = [];