export default class Dialog {

    static showDialog(title, data, type, okText, cancelText) {
        !this.isShow && (this.isShow = true);
        const that = this;
        return new Promise((resolve, reject) => {
            const ok = () => {
                document.body.removeChild(modalContainer);
                that.isShow = false;
                resolve('ok');
            };

            const cancel = () => {
                document.body.removeChild(modalContainer);
                that.isShow = false;
                reject('cancel');
            };

            const modalContainer = document.createElement('div');
            modalContainer.classList.add('ModalContainer');
            const modalPortal = document.createElement('div');
            modalPortal.classList.add('ModalPortal');
            if (document.body.clientWidth <= document.body.clientHeight) {
                modalPortal.style.transform = 'rotate(90deg)';
            }
            window.onresize = function(){
                if (document.body.clientWidth <= document.body.clientHeight) {
                    modalPortal.style.transform = 'rotate(90deg)';
                }
                else {
                    modalPortal.style.transform = 'rotate(0)';
                }
            }

            modalContainer.appendChild(modalPortal);

            const titleDiv = document.createElement('div');
            titleDiv.classList.add('Title');
            const titleData = document.createElement('span');
            titleData.innerText = title;
            const space = document.createElement('span');
            //space.style.flex = 1;
            space.classList.add('spaceSpan');
            const close = document.createElement('span');
            close.classList.add('Close');
            close.innerText = '';
            close.onclick = cancel;
            titleDiv.appendChild(titleData);
            titleDiv.appendChild(space);
            titleDiv.appendChild(close);
            modalPortal.appendChild(titleDiv);

            const content = document.createElement('div');
            content.classList.add('Content');
            modalPortal.appendChild(content);

            const contentData = document.createElement('div');
            contentData.classList.add('ContentData');
            type && contentData.classList.add(type);
            contentData.innerText = data;
            content.appendChild(contentData);

            const buttons = document.createElement('div');
            buttons.classList.add('Buttons');
            content.appendChild(buttons);

            const buttonOk = document.createElement('button');
            const buttonCancel = document.createElement('button');
            buttonOk.classList.add('ButtonOk');
            buttonCancel.classList.add('ButtonsCancel');
            buttonOk.innerText = okText || '';
            buttonCancel.innerText = cancelText || '';
            buttons.appendChild(buttonOk);
            buttons.appendChild(buttonCancel);

            buttonOk.onclick = ok;
            buttonCancel.onclick = cancel;


            document.body.appendChild(modalContainer);
        })
    }

    static info(data) {
        if (this.isShow)    return;
        return this.showDialog('', data, 'Info')
    }

    static warn(data) {
        if (this.isShow)    return;
        return this.showDialog('', data, 'Warn')
    }

    static error(data) {
        if (this.isShow)    return;
        return this.showDialog('', data, 'Error')
    }
}