		unlockChapterFinished: function (result, stayIfFail) {
            this.unlockChapterCallBack && this.unlockChapterCallBack(result, stayIfFail);
        },

        mallPay: function (result) {
			this.mallPayCallback && this.mallPayCallback(result);
			this.mallPayCallback = null;
        },

		checkIfMallPaySuccess: function (indentId) {
            const getData = (url, d) => {
                return Xhr.xhrSend(url, d).then(rs => {
                    if (rs.responseText.length === 0) {
                        return Promise.resolve(false);
                    }
                    let retData = JSON.parse(rs.responseText);
                    if (retData.status === 0) {
                        return Promise.resolve(retData.data.serial_num === indentId && retData.data.c_status == '1');
                    }
                    else if (retData.status === 100) {// 
                    	return Promise.resolve(false);
					}
                    else { // status>0 && status<9 
                        return Promise.resolve(false);
                    }
                }, rj => {
                    return Promise.resolve(false); //   
                });
            };

            let url = 'https://app.3000.com/?m=consume&op=index&ac=market_order_status&client=' + flashvars.client + '&v=' + flashvars.v +'&app_version=' + flashvars.app_version;
            let data = 'u=' + encodeURIComponent(Utils.aesEncrypt(flashvars.u, 1))
				+ '&token=' + encodeURIComponent(Utils.aesEncrypt(flashvars.token, 1))
				+ '&serial_num=' + encodeURIComponent(Utils.aesEncrypt(indentId, 1));

            return getData(url, data);
        },

		addAssets: function (url, type, key, isPlot) {
			const sKey = key || url;
			if (('img' === type && this.viewComponent.game.load.cache.checkImageKey(sKey)
				|| 'audio' === type && this.viewComponent.game.load.cache.checkSoundKey(sKey))
				&& !('ios' === flashvars.client && 'audio' === type && flashvars.app_version >= '1.5.0')) {
				return Promise.resolve();
			}
			return Utils.convertToAllowAccessControlOriginUrl(url).then(url => {
				if (!url) {
					return;
				}
				if ('img' === type) {
					this.viewComponent.game.load.image(sKey, url);
					!isPlot && ResourcesManager.addRefCount("img", sKey, 'global');
				}
				else if ('audio' === type) {
					let bAutoDecode = ResourcesManager.forceDecode.music === sKey || ResourcesManager.forceDecode.sounds.some(k => k === sKey);
					if ('ios' === flashvars.client && flashvars.app_version >= '1.5.0') {
                        PhoneUtils.loadAudioUrl(sKey, url, flashvars.offline);
					} else {
						this.viewComponent.game.load.audio(sKey, url, bAutoDecode);
                    }
                    if ('android' === flashvars.client) {
                        !isPlot && ResourcesManager.addRefCount("audio", sKey, 'global');
					} else {
                        !isPlot && ResourcesManager.addRefCount("audio", sKey, 'global');
					}

				}
			}, e => console.error("Invalid URL: " + e))
		},

		addImage: function (key, url, image) {
			this.viewComponent.game.cache.addImage(key, url || null, image);
		},

		addImageUrl: function (url, md5) {
			return new Promise((resolve,  reject) => {
				const data = new Image();
				data.src = url;
				data.onload = e => {
					if (!this.viewComponent.game.load.cache.checkImageKey(md5)) {
						this.viewComponent.game.cache.addImage(md5, url, e.target);
						resolve()
					}
				};
				data.onerror = data.onabort = e => reject(e);
			})
		},

		// Json Template
		parseJsonFile: function (game) {
			this.json_data = game;//JSON.parse(JSON.stringify(game));
			// load template
			const promises = [];
			if (this.json_data.template && this.json_data.template.json) {
				promises.push(this.parseTemplateCoverAssets());
				promises.push(this.parseTemplateDialogAssets());
				promises.push(this.parseTemplateMenuAssets());
				promises.push(this.parseTemplateSyMenuAssets());
				promises.push(this.parseTemplateMainMenuAssets());
				promises.push(this.parseTemplateSettingsAssets());
				promises.push(this.parseTemplateOptionAssets());
				promises.push(this.parseTemplatePlaybackAssets());
				promises.push(this.parseTemplateRecordAssets());
				promises.push(this.parseTemplateSoundAssets());
			}
			return Promise.all(promises).then(() => {
				if (false && this.json_data.font) {
                    return FontUtils.mergeFontImage(this.json_data.font, this.json_data.default_settings.font_size);
				}
				else {
					return Promise.resolve();
				}
			}).then(() => {
				//this.json_data = Utils.fixGame(this.json_data);
				promises.length = 0;

				// font image
                FontUtils.fontImage && this.viewComponent.game.load.image(FontUtils.fontFamily, FontUtils.fontImage);

                // load logo
				if (this.json_data.logo && this.json_data.logo.background && this.json_data.logo.background.url) {
					promises.push(this.addAssets(this.json_data.logo.background.url, 'img'));
				}

				// load cover
				if (this.json_data.cover.background && this.json_data.cover.background.url) {
					promises.push(this.addAssets(this.json_data.cover.background.url, 'img'));
				}
				if (this.json_data.cover.music && this.json_data.cover.music.url) {
                    let url = this.json_data.cover.music.url;
					promises.push(this.addAssets(url, 'audio'));
				}

				// load views
				const addEventAssets = (events) => {
					if (Object.prototype.toString.call(events) == '[object Array]') {
						events.forEach(e => {
							e.events.forEach(subEvent => {
								if (subEvent.type === 'playMusic' && subEvent.handle.music.url) {
									promises.push(this.addAssets(subEvent.handle.music.url, 'audio'));
								}
								else if (subEvent.type === 'playSound') {
									subEvent.handle.sounds.forEach(sound => {sound && sound.url != 'inherit' && promises.push(this.addAssets(sound.url, 'audio'));});
								}
							});
						});
					}
				};
				for (let p in this.json_data.viewMap) {
                    if (this.json_data.viewMap[p].type === 'timer') {
                        addEventAssets(this.json_data.viewMap[p].events);
                        continue;
                    }
                    const background = this.json_data.viewMap[p].content.background;
					const sound = this.json_data.viewMap[p].content.sound;
					if (!background) {
						continue;
					}
					if (background.url) {
						promises.push(this.addAssets(background.url, 'img'));
					}
					if (background.normal && background.normal.url) {
						promises.push(this.addAssets(background.normal.url, 'img'));

					}
					if (background.active && background.active.url) {
						promises.push(this.addAssets(background.active.url, 'img'));
					}
					if (background.progress && background.progress.url) {
						promises.push(this.addAssets(background.progress.url, 'img'));

					}
					if (sound && sound.url) {
						promises.push(this.addAssets(sound.url, 'audio'));
					}
					addEventAssets(this.json_data.viewMap[p].events);
					this.json_data.viewMap[p].children.forEach(child => {addEventAssets(child.events)});
				}
				return Promise.all(promises).then(() => {
                    return Promise.resolve();
                    //this.sendNotification( diygamePlayer.AppConstants.SCENE_PRELOADING_PROGRESS); // 
				}, e => Promise.reject(e))
			});
		},

